import React from 'react';
import DefaultLayout from './containers/DefaultLayout';

const Breadcrumbs = React.lazy(() => import('./views/Base/Breadcrumbs'));
const Cards = React.lazy(() => import('./views/Base/Cards'));
const Typography = React.lazy(() => import('./views/Theme/Typography'));
const Dashboard = React.lazy(() => import('./views/Dashboard'));

const Mahasiswa = React.lazy(() => import('./views/Crud/Mahasiswa/Mahasiswa'))

const Kosong = React.lazy(() => import('./views/Contoh/Kosong.js'));
const Form = React.lazy(() => import('./views/Contoh/Forms.js'));
const Tabel = React.lazy(() => import('./views/Contoh/Tables.js'));

const MhsAdd = React.lazy(() => import('./views/Crud/Mahasiswa/MhsAdd'));


// https://github.com/ReactTraining/react-router/tree/master/packages/react-router-config
const routes = [
  { path: '/', exact: true, name: 'Home', component: DefaultLayout },
  { path: '/dashboard', name: 'Dashboard', component: Dashboard },
  { path: '/theme/typography', name: 'Typography', component: Typography },
  { path: '/base/cards', name: 'Cards', component: Cards },
  { path: '/base/breadcrumbs', name: 'Breadcrumbs', component: Breadcrumbs },
  { path: '/crud/mahasiswa', exact: true, name: 'Mahasiswa', component: Mahasiswa },
  { path: '/contoh/kosong', name: 'Kosong', component: Kosong },
  { path: '/contoh/form', name: 'Form', component: Form },
  { path: '/contoh/tabel', name: 'Tabel', component: Tabel },

  { path: '/crud/mahasiswa/create', name: 'Create Mahasiswa', component: MhsAdd },
];

export default routes;
